@use 'base/variables' as v;
@use 'base/mixins' as m;

.logo {
    position: absolute;
    top: 2.5rem;
    left: 1.5rem;
    padding: 0;
    margin: 0;

    @include m.tablet {
        top: 2rem;
        left: 2rem;
    }

    &__barraUno,
    &__barraCuatro,
    &__barraDos,
    &__barraTres,
    &__barraMedio,
    &__titulo-h1,
    &__subTitulo {
        display: inline-block;
        position: absolute;
        border-radius: 2rem;
    }

    &__barraUno,
    &__barraCuatro,
    &__barraDos,
    &__barraTres {
        width: 0.8rem;
        height: 3rem;

        @include m.tablet {
            width: 1rem;
            height: 3rem;
        }
    }

    &__barraUno,
    &__barraCuatro {
        background-image: linear-gradient(#F43AB0, #FF7192);

    }

    &__barraDos,
    &__barraTres {
        background-image: linear-gradient(#6703F9, #C920C7);
    }

    &__barraUno {
        top: -1.8rem;
        transform: rotate(45deg);
        left: 1rem;

        @include m.tablet {
            top: 0;
        }
    }

    &__barraDos {
        top: -.2rem;
        transform: rotate(-45deg);
        left: 1rem;

        @include m.tablet {
            top: 1.5rem;
        }
    }

    &__barraMedio {
        background-image: linear-gradient(#6703F9, #FF7192);
        transform: skew(-15deg, 0deg);
        width: 0.8rem;
        height: 3rem;
        border-radius: 4rem 2rem 4rem 2rem;
        top: -1rem;
        left: 3rem;

        @include m.tablet {
            display: none;
        }
    }

    &__barraTres {
        top: -1.8rem;
        left: 5rem;

        @include m.tablet {
            left: 22.6rem;
            top: 0rem;
        }

        transform: rotate(-45deg);
    }

    &__barraCuatro {
        top: -.2rem;
        left: 5rem;

        @include m.tablet {
            left: 22.6rem;
            top: 1.5rem;
        }

        transform: rotate(45deg);
    }

    &__titulo-h1 {
        display: none;
        color: #fff;
        font-size: 3rem;
        width: max-content;
        top: 9.0rem;
        left: -4rem;

        @include m.tablet {
            display: block;
            top: 0rem;
            left: 3rem;
        }
    }

    &__subTitulo {
        left: 3.5rem;
        top: 3.4rem;
        font-size: 1.4rem;
        width: max-content;
        background-image: linear-gradient(#6703F9, #C920C7);
        color: transparent;
        background: linear-gradient(to right, #FF7192 0%, #C920C7 100%);
        -webkit-background-clip: text;
        background-clip: text;
    }
}